.timeline {
	width: 100%;
}

.timeline.timeline-one .timeline-item {
	position: relative;
	width: 50%;
	margin-left: 50%;
	padding-bottom: 1.5rem;
	padding-left: 1.5rem;
	text-align: left;
}

@media (max-width: 575.98px) {
	.timeline.timeline-one .timeline-item {
		width: 100%;
		margin-left: 0;
	}
}
.timeline.timeline-one .timeline-item img {
	margin-bottom: 1.25rem;
}

.timeline.timeline-one .timeline-item:before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 16px;
	height: 16px;
	margin-top: 0.425rem;
	margin-left: -0.5rem;
	border: 0.0625rem solid #dce6f3;
	border-radius: 50%;
}

.timeline.timeline-one .timeline-item:after {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	bottom: -0.1875rem;
	width: 0.0625rem;
	margin-top: 1.875rem;
	margin-left: -0.0625rem;
	background-color: #dce6f3;
}

.timeline.timeline-one .timeline-item:nth-child(2n+1) {
	margin-left: 0;
	padding-right: 1.5rem;
	padding-left: 0;
	text-align: right;
}

@media (max-width: 575.98px) {
	.timeline.timeline-one .timeline-item:nth-child(2n+1) {
		padding-right: 0;
		padding-left: 1.5rem;
		text-align: left;
	}
}
.timeline.timeline-one .timeline-item:nth-child(2n+1):after,
.timeline.timeline-one .timeline-item:nth-child(2n+1):before {
	right: 0;
	left: auto;
}

@media (max-width: 575.98px) {
	.timeline.timeline-one .timeline-item:nth-child(2n+1):after,
.timeline.timeline-one .timeline-item:nth-child(2n+1):before {
		right: auto;
		left: 0;
	}
}
.timeline.timeline-one .timeline-item:nth-child(2n+1):before {
	margin-right: -0.5rem;
}

.timeline.timeline-one .timeline-item:nth-child(2n+1):after {
	margin-right: -0.0625rem;
}

.timeline.timeline-one.dark .timeline-item:before {
	border: 0.0625rem solid #b7c3d2;
}

.timeline.timeline-one.dark .timeline-item:after {
	background-color: #b7c3d2;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: "Playfair Display", Georgia, "Times New Roman", serif;
}

.display-4 {
	font-size: 2.5rem;
}
@media (min-width: 768px) {
	.display-4 {
		font-size: 3rem;
	}
}

.nav-scroller {
	position: relative;
	z-index: 2;
	height: 2.75rem;
	overflow-y: hidden;
}
.nav-scroller .nav {
	display: flex;
	flex-wrap: nowrap;
	padding-bottom: 1rem;
	margin-top: -1px;
	overflow-x: auto;
	text-align: center;
	white-space: nowrap;
	-webkit-overflow-scrolling: touch;
}

.card-img-right {
	height: 100%;
	border-radius: 0 3px 3px 0;
}

.flex-auto {
	flex: 0 0 auto;
}

/* Pagination */
.blog-pagination {
	margin-bottom: 4rem;
}

.blog-pagination > .btn {
	border-radius: 2rem;
}

/*
 * Blog posts
 */
.blog-post {
	margin-bottom: 4rem;
}

.blog-post-title {
	margin-bottom: 0.25rem;
	font-size: 2.5rem;
}

.blog-post-meta {
	margin-bottom: 1.25rem;
	color: #727272;
}

.divider__dotted-white {
	border-bottom: 1px dotted #fff;
}

.embed-container {
	position: relative;
	padding-bottom: 56.25%;
	overflow: hidden;
	max-width: 100%;
	height: auto;
}
.embed-container iframe,
.embed-container object,
.embed-container embed {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}